import { Component, OnInit, OnChanges, AfterContentInit } from '@angular/core';
import { OrderServices } from '../order.service';
import { Router } from '@angular/router';
import ds from '../da.service';
import { environment } from '../../environments/environment';
import { NotificationsService } from 'angular2-notifications';
import { AuthService } from '../service/auth.service';
import { Page } from '../models/page.model';
@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {

  lat: number;
  lng: number;
  loading;
  loadingexternal;
  page = 1;
  providerId;
  totalPages = 1;
  currentPage = new Page();
  noMoreData;
  newlat;
  newLng;
  externalCurrentPage;
  externalTotalPages;
  externalLoading;
  externalPage;
  noMoreExternalData;
  loadingExternalOrder;
  loadingNormalOrder;

  orders: any[] = [];
  types = ['alert', 'error', 'info', 'warn', 'success'];
  animationTypes = ['fromRight', 'fromLeft', 'scale', 'rotate'];
  constructor(private orderServices: OrderServices,
    private authService: AuthService,
    private _notifications: NotificationsService,
    private router: Router) {
  }
  ngOnInit() {

    let user = this.authService.currentUser.user;
    this.providerId = user.id;
    this.loginDeapStream();
    this.getProviderLocation();
    this.fetchOrders(1);
    this.fetchproviderExternalorders(1);

  }



  newPosition(event) {
    this.newlat = event.coords.lat;
    this.newLng = event.coords.lng;
    this.lat = this.newlat;
    this.lng = this.newLng;
    // this.open('the new postion is ')
    let body = {
      "currentLocation": [
        this.lng,
        this.lat
      ]
    };
    this.orderServices.updateprovideLocation(this.providerId, body)
      .subscribe(result => {
        //console.log(result, 'result')
      }
      )

    //ds.event.emit('dsLocationUpdate', { id: environment.id, latitude: this.newlat, longitude: this.newLng });

  }

  getProviderLocation() {
    this.orderServices.getProviderLocation(this.providerId)
      .subscribe(result => {
        this.lng = result['currentLocation'][0],
          this.lat = result['currentLocation'][1]
      }
      )
  }



  onLoadMoreClick() {
    this.page++;
    this.fetchOrders(this.page);
  }


  open(text) {
    const toast = this._notifications.success(text, 'Click to undo...', {
      timeOut: 3000,
      showProgressBar: true,
      pauseOnHover: true,
      clickToClose: true,
      maxLength: 10,
      clickIconToClose: true
    });
    // this._notifications.success('new notification', 'this notification from my orders ');
  }

  fetchOrders(page) {

    this.loadingNormalOrder = true;
    this.orderServices.fetchproviderorders(this.providerId, page)
      .subscribe(result => {
        this.currentPage = result;
        this.orders = this.orders.concat(result.data);

        this.totalPages = result.pageCount;

        this.loadingNormalOrder = false;

        //listen

        if (this.page == this.totalPages)
          this.noMoreData = true;

      }
      )
  }
  fetchproviderExternalorders(page) {

    this.loadingExternalOrder = true;
    this.orderServices.fetchproviderExternalorders(this.providerId, page)
      .subscribe(result => {
        this.externalCurrentPage = result;
        this.externalTotalPages = result.pageCount;
        console.log(this.externalTotalPages,'the external order===',  this.externalCurrentPage);

        this.loadingExternalOrder = false;

        //listen

        if (this.externalPage == this.externalTotalPages)
          this.noMoreExternalData = true;

      }
      )
  }

  onPageChange(event) {

    this.fetchOrders(event.offset + 1);
  }

  onExternalPageChange(event){
    this.fetchproviderExternalorders(event.offset + 1);
  }

  showOrder(orderId) {
    this.router.navigate(['orders', orderId]);
  }
  showExternalOrder(orderId) {
    this.router.navigate(['external-orders', orderId]);
  }

  // startTracking() {
  //   let opts = { maximumAge: 60000, timeout: 15000 }
  //   let watchPositionReturn = navigator.geolocation.watchPosition((position) => {
  //     this.lat = position['coords'].latitude;
  //     this.lng = position['coords'].longitude;
  //     // console.log('watchPositionReturn', position);
  //     //  ds.event.emit('dsLocationUpdate', { id: environment.id, latitude: this.lat, longitude: this.lng });
  //   },
  //     (err) => {
  //       console.log('watchPositionReturn error', err);
  //     },
  //     opts);
  // }

  loginDeapStream() {
    let user = this.authService.currentUser.user;
    ds.login({
      accessToken: this.authService.getToken(),
      loginAs: "PROVIDER",
      id: user.id
    }, (success, data) => {

    });
  }

}
