import { Component, OnInit, NgZone, ElementRef, ViewChild, ApplicationRef, Input, EventEmitter, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { OrderServices } from '../order.service';
import { environment } from '../../environments/environment';
import ds from '../da.service';
import { NotificationsService } from 'angular2-notifications';
import swal from 'sweetalert2'
import { AuthService } from '../service/auth.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { NgxSmartModalService, NgxSmartModalComponent } from 'ngx-smart-modal';
import * as moment from 'moment'
declare var $;
@Component({
  selector: 'app-order-detail',
  templateUrl: './order-detail.component.html',
  styleUrls: ['./order-detail.component.css']
})
export class OrderDetailComponent implements OnInit {
  orderId;
  @ViewChild('laterPaymentOverlay') laterPaymentOverlay: ElementRef;
  @Input() rating: number;
  @Input() itemId: number;
  @Output() ratingClick: EventEmitter<any> = new EventEmitter<any>();
  inputName: string;
  cancelOrder = false
  cancelForm: FormGroup;
  order;
  rateError = false;
  newlat;
  newLng;
  lat: number;
  lng: number;
  client;
  providerId;
  rateClientForm: FormGroup;
  currentStatus;
  openOverly = false;
  reasons;
  cancelStatus;
  loadingReason;
  requestToPauseByClient = false;
  second = 0;
  minute = 0;
  firstDsLisning = false;
  hour = 0;
  stopedTime = 0;
  timer;
  paymentMethod = false;
  timerIntervel;
  currentOrderStartTime;
  Payment = false;

  constructor(private route: ActivatedRoute,
    private _notifications: NotificationsService,
    private zone: NgZone,
    public ngxSmartModalService: NgxSmartModalService,
    private router: Router,
    private orderServices: OrderServices,
    private authService: AuthService,
    private appRef: ApplicationRef
  ) { }

  ngOnInit() {
    let user = this.authService.currentUser.user;
    this.providerId = user.id;
    this.orderId = this.route.snapshot.params['id'];

    this.inputName = this.itemId + '_rating';

    this.loginDeapStream();
    this.getProviderLocation();


    this.cancelForm = new FormGroup({
      'reasonText': new FormControl(null),
      'reason': new FormControl(null)
    });

    this.rateClientForm = new FormGroup({
      'comment': new FormControl(null, [Validators.required])
    });

    this.fetchOrder(this.orderId);

  }
  newPosition(event) {
    this.newlat = event.coords.lat;
    this.newLng = event.coords.lng;
    this.lat = this.newlat;
    this.lng = this.newLng;
    // this.open('the new postion is ')
    let body = {
      "currentLocation": [
        this.lng,
        this.lat
      ]
    };
    this.orderServices.updateprovideLocation(this.providerId, body)
      .subscribe(result => {

      }
      )

    //ds.event.emit('dsLocationUpdate', { id: environment.id, latitude: this.newlat, longitude: this.newLng });

  }

  getProviderLocation() {
    this.orderServices.getProviderLocation(this.providerId)
      .subscribe(result => {
        this.lng = result['currentLocation'][0],
          this.lat = result['currentLocation'][1]
      }
      )
  }

  dsEventLisenning() {
    ds.record.getRecord(`dsOrder/${this.orderId}`).subscribe(
      (res) => {

        console.log('the notification is =======>', res);
        if (res.status == 'WAITING_FOR_PAYMENT' && this.currentStatus != 'ENDED' && this.order.isProviderWaitingForPayment == true) {
          this.Payment = true;
          this.openNav();
        }
        if (res.status == 'ENDED' && this.order.isProviderWaitingForPayment == true) {
          this.closeNav()
          this.paymentMethod = true;
        }

        if (res.status == 'FINISHED_WORKING' || this.currentStatus == 'FINISHED_WORKING')
          this.openNav();

        if (res.status == 'ENDED' && this.order.isProviderWaitingForPayment == true) {
          this.closeNav()
          if (res.paymentMethod == 'CASH')
            this.paymentMethod = true;
        }

        if (res.status == 'ENDED' && this.order.isProviderWaitingForPayment == false && this.order.rate == 0)
          this.ngxSmartModalService.getModal('rateClientModal').open()

        if (res.requestToPauseByClient == true) {
          swal({
            title: 'PAUSED_BY_CLIENT',
            text: "the client ask to pause this order",
            type: 'info',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
          }).then(isConfirm => {
            if (isConfirm.value === true) {
              this.pauseResume('PAUSED_BY_CLIENT');
            } else {
              this.denyPausation();
            }
          })

          // let confirm = window.confirm('are you want to PAUSED_BY_CLIENT');
          // if (confirm == true) {
          //   this.pauseResume('PAUSED_BY_CLIENT');
          // }
        }

        if (res.requestToResumeByClient == true) {

          swal({
            title: 'RESUEME_BY_CLIENT',
            text: "the client ask to resume this order",
            type: 'info',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
          }).then((isConfirm) => {
            if (isConfirm.value === true) {
              this.pauseResume('IN_PROGRESS');
            } else {
              this.denyResumption();
            }
          })
          // let confirm = window.confirm('are you want to RESUEME_BY_CLIENT');
          // if (confirm == true) {
          //   this.pauseResume('IN_PROGRESS');
          // }
        }

        if (res.status != this.currentStatus) {
          this.zone.run(() => {
            this.fetchOrder(this.orderId);
          });


        }
        if (res.status == "IN_PROGRESS" && this.currentStatus != "IN_PROGRESS")
          this.closeNav()

        if (res.status == "IN_PROGRESS" || this.currentStatus == "IN_PROGRESS") {

          if (res.currentStartTime) this.currentOrderStartTime = res.currentStartTime
          if (this.firstDsLisning == false) {
            this.zone.run(() => {
              this.orderTimer();
            });
          }

        }

        if ((res.status == "PAUSED_BY_PROVIDER" && !res.requestToResumeByProvider == true)
          || (res.status == "PAUSED_BY_CLIENT" && !res.requestToResumeByClient == true)) {
          this.stopTimer();

        }


      });
  }

  pauseResume(status) {
    this.orderServices.updateproviderorders(this.orderId, status).subscribe(
      res => {
      },
      err => {
        console.log('the error is ====>', err);
      })
  }

  fetchOrder(orderId) {
    this.orderServices.getOrderById(this.providerId, orderId).subscribe(res => {
      this.order = res;
      this.dsEventLisenning();

      this.orderServices.getClientById(this.order.client.id).subscribe(result => {
        this.client = result;

        if (['ACCEPTED', 'ON_THE_WAY', 'ARRIVED'].includes(this.order.status)) this.cancelOrder = true;
        this.currentStatus = this.order.status;
        if (this.currentStatus == 'ENDED' && this.order.isProviderWaitingForPayment == true) {
          this.closeNav()
          this.paymentMethod = true;
        } else {
          this.paymentMethod = false;
        }

        if (this.currentStatus == 'ENDED' && this.order.isProviderWaitingForPayment == false && this.order.rate == 0)
          this.ngxSmartModalService.getModal('rateClientModal').open()

        if (['PAUSED_BY_PROVIDER', 'IN_PROGRESS', 'PAUSED_BY_CLIENT'].includes(this.order.status)) {
          this.currentTime();
        }
        if (this.currentStatus == 'WAITING_FOR_PAYMENT' && this.order.isProviderWaitingForPayment == true) {
          this.Payment = true;
          this.openNav();
        }


        console.log('the order is', this.order);
      })
    })
  }

  currentTime() {

    this.resuemeOrderPeriodTime();
    this.second = 0;
    this.minute = 0;
    this.hour = 0;
    let currentTime;
    if (this.stopedTime > 0) currentTime = this.secondsToHMS(this.stopedTime + moment().diff(this.currentOrderStartTime, 'seconds'));
    else currentTime = this.secondsToHMS(moment().diff(this.currentOrderStartTime, 'seconds'));
    // if (this.stopedTime == 0) currentTime = this.secondsToHMS(moment().diff(this.currentOrderStartTime, 'seconds'));
    // else currentTime = this.secondsToHMS(this.stopedTime);
    this.second = currentTime.seconds;
    this.minute = currentTime.minutes;
    this.hour = currentTime.hours;

    this.timer = `${this.hour} : ${this.minute} : ${this.second} `;
    this.appRef.tick();
  }

  providerConfirmCash(action) {
    let actionBody = { action: action };

    this.orderServices.providerConfirmCash(this.providerId, this.orderId, actionBody).subscribe(
      res => {
        if (this.openOverly == true) this.closeNav()
        this.zone.run(() => {
          this.fetchOrder(this.orderId)
        });

        console.log('providerConfirmCash', res)
      });
  }

  denyResumption() {
    this.orderServices.denyResumption(this.providerId, this.orderId).subscribe(
      res => {
        console.log('denyResumption', res)
      });
  }

  denyPausation() {
    this.orderServices.denyPausation(this.providerId, this.orderId).subscribe(
      res => {
        console.log('denyPausation', res)
      });
  }

  open(text) {
    const toast = this._notifications.success(text, 'Click to undo...', {
      timeOut: 3000,
      showProgressBar: true,
      pauseOnHover: true,
      clickToClose: true,
      maxLength: 10,
      clickIconToClose: true
    });
  }

  changeStatus(id, status) {
    if (status == 'CANCELLED_BY_PROVIDER' || status == 'REJECTED_BY_PROVIDER') {

      switch (this.currentStatus) {
        case 'ACCEPTED': this.cancelStatus = 'CANCELLATION_ACCEPTED_REASON_BY_PROVIDER';
          break;

        case 'ARRIVED': this.cancelStatus = 'CANCELLATION_ARRIVED_REASON_BY_PROVIDER';
          break;

        case 'ON_THE_WAY': this.cancelStatus = 'CANCELLATION_ON_THE_WAY_REASON_BY_PROVIDER';
          break;

        case 'IN_PROGRESS': this.cancelStatus = 'CANCELLATION_IN_PROGRESS_REASON_BY_PROVIDER';
          break;

        case 'PENDING': this.cancelStatus = 'REJECTION_PENDING_REASON_BY_PROVIDER';
          break;
        default: return
      }
      this.loadingReason = true
      this.orderServices.getReasons(this.cancelStatus).subscribe(
        res => {
          this.loadingReason = false
          this.reasons = res;
        }
      )
    }



    this.orderServices.updateproviderorders(id, status).subscribe(
      res => {
        if (status == 'CANCELLED_BY_PROVIDER' || status == 'REJECTED_BY_PROVIDER') $('#myModal').modal('show');

      },
      err => {
        this.open(err.error.error.message)
        console.log('the error is ====>', err);
      })

  }

  onCancelledOrder() {
    let reasonText = this.cancelForm.get('reasonText').value;
    let reason = this.cancelForm.get('reason').value;
    if (!this.cancelForm.valid) {
      Object.keys(this.cancelForm.controls).forEach(key => {
        this.cancelForm.get(key).markAsTouched();
      })
      return;
    }
    let reasonBody = { 'reasonType': this.cancelStatus };

    if (reason) reasonBody['reason'] = reason;
    if (!reason && reasonText) reasonBody['otherReason'] = reasonText;



    this.orderServices.addReasons(this.orderId, reasonBody).subscribe(
      res => {
        $('#myModal').modal('hide')
      },
      err => {
        console.log('the error of reason', err);
      }
    )
  }

  goToChat(client) {
    this.router.navigate(['orders', this.orderId, 'client', client.id, 'chat']);

  }

  loginDeapStream() {
    let user = this.authService.currentUser.user;
    ds.login({
      accessToken: this.authService.getToken(),
      loginAs: "PROVIDER",
      id: user.id
    }, (success, data) => {

    });
  }

  openNav() {
    this.laterPaymentOverlay.nativeElement.style.width = "100%";
    this.openOverly = true;
  }

  closeNav() {
    this.laterPaymentOverlay.nativeElement.style.width = "0%";
    this.openOverly = false;
  }

  laterPayment() {
    this.orderServices.laterPayment(this.orderId).subscribe(res => {

      this.closeNav()
      // this.fetchOrder(this.orderId);
    })
    console.log('later payment');
  }


  resuemeOrderPeriodTime() {

    let workingPeriods = this.order.workingPeriods;
    this.stopedTime = 0;
    if (workingPeriods) {

      for (let workingPeriod of workingPeriods) {

        if (workingPeriod.endTime) {

          let endTime = moment(workingPeriod.endTime), startTime = moment(workingPeriod.startTime);
          // this.stopedTime += moment.duration(endTime.diff(startTime)).asSeconds();
          this.stopedTime += endTime.diff(startTime, 'seconds')
        }
      }
    }
    console.log(111111, this.stopedTime)
    return this.stopedTime;
  }


  secondsToHMS(d) {
    d = Number(d);
    const h = Math.floor(d / 3600);
    const m = Math.floor((d % 3600) / 60);
    const s = Math.floor((d % 3600) % 60);

    return { hours: h, minutes: m, seconds: s };
  }

  orderTimer() {
    console.log(1111111111111);
    this.resuemeOrderPeriodTime();
    // clearInterval(this.timerIntervel);
    this.firstDsLisning = true;
    let currentTime;
    if (this.stopedTime > 0) currentTime = this.secondsToHMS(this.stopedTime + moment().diff(this.currentOrderStartTime, 'seconds'));
    else currentTime = this.secondsToHMS(moment().diff(this.currentOrderStartTime, 'seconds'));

    this.second = currentTime.seconds;
    this.minute = currentTime.minutes;
    this.hour = currentTime.hours;

    this.timerIntervel = setInterval(() => {

      this.second = this.second + 1
      if (this.second >= 60) {
        this.second = 0;
        this.minute++;
      }
      if (this.minute >= 60) {
        this.second = 0;
        this.minute = 0;
        this.hour++;
      }
      this.timer = `${this.hour} : ${this.minute} : ${this.second} `;
      this.appRef.tick();

    }, 1000);
    // console.log('second timer enter in timer ', this.timer);
  }

  stopTimer() {
    this.firstDsLisning = false
    console.log('second ', this.second, this.minute, this.hour);
    clearInterval(this.timerIntervel);
    this.appRef.tick();
  }

  onClick(rating: number): void {
    this.rating = rating;
    this.ratingClick.emit({
      itemId: this.itemId,
      rating: rating
    });
    console.log('the rate====', this.rating);
  }

  onRateClientOrder() {

    if (!this.rateClientForm.valid) {
      Object.keys(this.rateClientForm.controls).forEach(key => {
        this.rateClientForm.get(key).markAsTouched();
      })
      if (!this.rating || this.rating == 0) this.rateError = true;

      return;
    }


    let comment = this.rateClientForm.get('comment').value;
    let rateBody = { rate: this.rating };

    if (comment) rateBody['comment'] = comment;

    this.orderServices.rateOrder(this.orderId, rateBody)
      .subscribe(res => {
        this.ngxSmartModalService.getModal('rateClientModal').close()
        console.log('the rate result ====>', res);
      },
        err => {
          console.log('the error ==>', err);
        })

  }
}

