import { Component, OnInit, NgZone, ApplicationRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationsService } from 'angular2-notifications';
import { OrderServices } from '../order.service';
import { AuthService } from '../service/auth.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { NgxSmartModalService, NgxSmartModalComponent } from 'ngx-smart-modal';
import { DatepickerOptions } from 'ng2-datepicker';
import ds from '../da.service';
declare var $;
@Component({
  selector: 'app-external-order-show',
  templateUrl: './external-order-show.component.html',
  styleUrls: ['./external-order-show.component.css']
})
export class ExternalOrderShowComponent implements OnInit {
  calendarOptions = {
    format: "DD-mm-YYYY",
    displayFormat: 'MMM D[,] YYYY',
    barTitleFormat: 'MMMM YYYY',
    minDate: new Date(Date.now()),
    firstWeekdaySunday: false
  };

  orderId;
  cancelOrder = false
  order;
  newlat;
  loadingReason;
  newLng;
  cancelForm: FormGroup;
  lat: number;
  lng: number;
  externalPriceForm: FormGroup;
  externalFinishForm: FormGroup;
  client;
  priceMessage;
  priceRangeMessage = false
  providerId;
  currentStatus;
  openOverly = false;
  reasons;
  cancelStatus;
  loadingStatus;
  requestToPauseByClient = false;
  date: Date;

  constructor(private route: ActivatedRoute,
    private _notifications: NotificationsService,
    private zone: NgZone,
    private router: Router,
    private orderServices: OrderServices,
    public ngxSmartModalService: NgxSmartModalService,
    private authService: AuthService,
    private appRef: ApplicationRef) { }

  ngOnInit() {
    // this.date = new Date();
    let user = this.authService.currentUser.user;
    this.providerId = user.id;
    this.orderId = this.route.snapshot.params['id'];
    this.fetchOrder(this.orderId);
    this.getProviderLocation();
    this.externalPriceForm = new FormGroup({
      'minPrice': new FormControl(null, [Validators.required, Validators.min(1), Validators.pattern("[0-9]*")]),
      'maxPrice': new FormControl(null, [Validators.required, Validators.min(1), Validators.pattern("[0-9]*")]),
    });

    this.externalFinishForm = new FormGroup({
      'deliveryDate': new FormControl(null, [Validators.required]),
      'price': new FormControl(null, [Validators.required, Validators.min(1), Validators.pattern("[0-9]*")])
    });

    this.cancelForm = new FormGroup({
      'reasonText': new FormControl(null),
      'reason': new FormControl(null)
    });


    this.loginDeapStream();
    this.fetchOrderReasons();

  }

  fetchOrder(orderId) {


    this.orderServices.getExternalOrderById(this.providerId, orderId)
      .subscribe(res => {
        this.order = res;

        this.orderServices.getClientById(this.order.client.id)
          .subscribe(result => {
            this.client = result;
            this.currentStatus = this.order.status;
            this.dsEventLisenning()
          })
        console.log('the order is', this.order);

      })
  }


  acceptStatus() {
    $('#myModal').modal('show');
  }

  finshExternalOrderStatus() {
    this.ngxSmartModalService.getModal('finishModal').open()
  }

  changeStatus(id, status) {

    let body = { "status": status };
    if (status == 'ACCEPTED_BY_PROVIDER') {
      let minPrice = this.externalPriceForm.get('minPrice').value;
      let maxPrice = this.externalPriceForm.get('maxPrice').value;
      if (!this.externalPriceForm.valid) {
        Object.keys(this.externalPriceForm.controls).forEach(key => {
          this.externalPriceForm.get(key).markAsTouched();
        })
        return;
      }
      body['minPrice'] = minPrice;
      body['maxPrice'] = maxPrice;
    }

    if (status == 'FINISHED_WORKING') {
      let price = this.externalFinishForm.get('price').value;
      let deliveryDate = this.externalFinishForm.get('deliveryDate').value;
      if (!this.externalFinishForm.valid) {
        Object.keys(this.externalFinishForm.controls).forEach(key => {
          this.externalFinishForm.get(key).markAsTouched();
        })
        return;
      }
      body['price'] = price;
      body['deliveryDate'] = deliveryDate.getTime();
      body['deliveryLocation'] = [this.lng, this.lat];
    }
    console.log('the body ===>', body);
    this.loadingStatus = true;
    this.orderServices.updateExternalOrders(id, body)
      .subscribe(res => {
        this.loadingStatus = false;
        this.ngxSmartModalService.getModal('finishModal').close()
        $('#myModal').modal('hide');

        if (res['status'] == 'REJECTED_BY_PROVIDER') this.ngxSmartModalService.getModal('externalOrderReasonModal').open()

        console.log('the return from updated order is ===>', res);
      },
        err => {
          this.loadingStatus = false;
          console.log(err.error.error.message, err.status);
          this.priceRangeMessage = true
          this.priceMessage = err.error.error.message
          if (err.status == 422) this.priceMessage = err.error.error.message
        })
  }

  newDeliveryPosition(event) {
    this.newlat = event.coords.lat;
    this.newLng = event.coords.lng;
    this.lat = this.newlat;
    this.lng = this.newLng;
    //ds.event.emit('dsLocationUpdate', { id: environment.id, latitude: this.newlat, longitude: this.newLng });

  }

  getProviderLocation() {
    this.orderServices.getProviderLocation(this.providerId)
      .subscribe(result => {
        this.lng = result['currentLocation'][0],
          this.lat = result['currentLocation'][1]
      }
      )
  }

  goToChat(client) {
    this.router.navigate(['orders', this.orderId, 'client', client.id, 'chat']);

  }

  loginDeapStream() {
    let user = this.authService.currentUser.user;
    ds.login({
      accessToken: this.authService.getToken(),
      loginAs: "PROVIDER",
      id: user.id
    }, (success, data) => {

    });
  }

  dsEventLisenning() {
    ds.record.getRecord(`dsOrder/${this.orderId}`).subscribe(
      (res) => {
        // if (res.status == 'REJECTED_BY_PROVIDER') this.ngxSmartModalService.getModal('externalOrderReasonModal').open()
        if (this.currentStatus != res.status) {
          this.appRef.tick();
          this.fetchOrder(this.orderId)
        }
        console.log('the notification is =======>', res);
      });
  }

  fetchOrderReasons() {
    this.loadingReason = true
    this.cancelStatus = 'REJECTED_BY_PROVIDER_REASON';
    this.orderServices.getExternalOrderReasons(this.cancelStatus).subscribe(
      res => {
        this.loadingReason = false
        this.reasons = res;
      }
    )
  }

  onCancelledOrder() {
    let reasonText = this.cancelForm.get('reasonText').value;
    let reason = this.cancelForm.get('reason').value;
    if (!this.cancelForm.valid) {
      Object.keys(this.cancelForm.controls).forEach(key => {
        this.cancelForm.get(key).markAsTouched();
      })
      return;
    }
    let reasonBody = { 'reasonType': this.cancelStatus };

    if (reason) reasonBody['reason'] = reason;
    if (!reason && reasonText) reasonBody['otherReason'] = reasonText;



    this.orderServices.addExternalReasons(this.orderId, reasonBody).subscribe(
      res => {
        this.ngxSmartModalService.getModal('externalOrderReasonModal').close()
      },
      err => {
        console.log('the error of reason', err);
      }
    )
  }
}
